export default {
    data() {
        return {
			shopObj:{},
			phone:"",
			phones:"",
			code:"",
			codeSend:false,
			time:60,
			disabled:false,//falsey为绑定手机，true为更换
        }
    },
	methods: {
		checkPhone(phone){
			if(!(/^1[3456789]\d{9}$/.test(phone))){
				return false;
			}
			return true
		},
		sendCode(){
		 	var that=this
		 	if(that.codeSend){
		 		return;
		 	}
			if(!that.checkPhone(that.phone)){
				uni.showToast({
					title:"请填写正确的手机号码",
					icon:"none"
				})
				return
			}
		 	that.codeSend=true;
		 	var t=setInterval(function(){
		 		that.time-=1;
		 		if(that.time==0){
		 			that.codeSend=false;
		 			that.time=60;
		 			clearInterval(t)
		 		}
		 	},1000)
		 	this.$ajax({
		 		url:"/sms",
				data:{
					phone: that.phone,
					type: "SMS_161591477"
				}
		 	})
		},
		sava(){
			let that=this;
			if(!this.code||!this.phone){
				uni.showToast({
				    icon: 'none',
				    title: '请输入完整的信息'
				});
				return;
			}
			that.$ajax({
				url:"/system/bindPhone",
				data:{
					code: that.code,
					managerId: that.shopObj.managerId,
					phone: that.phone,
					type: "SMS_161591477"
				},success(){
					uni.showToast({
						title:"操作成功,请重新登录",
						icon:"none"
					})
					setTimeout(function(){
						uni.reLaunch({
							url: '/pages/login/login'
						});
					},1000)
				}
			})
		}
	},
	 onReady() {
		 if(uni.getStorageSync("phone")){
			this.phone=uni.getStorageSync("phone")||"";
			this.phones=uni.getStorageSync("phone")||""
			this.disabled=true
		 }
	},
	onLoad(){
		let that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
	}
}
